              text: 'Trust Domains currently has four blockchain domain names, namely .defi, .cfx, .bnb, .ht, which are deployed on Ethereum, Conflux, Binance Smart Chain, Hecochain.'
          }
        ],
        collapsesCN: [
          {
              name: "1",
              title: '什么是区块链域名?',
              text: '区块链域是区块链上的一种类型资产。区块链域名旨在通过智能合约解析您的网络和钱包地址。这是您的加密货币之旅的友好第一步。'
