          }
        ],
  }),
  mounted() {
    this.$nextTick(() => {
      window.addEventListener('resize', this.handleWindowResize)
    })
    this.setHeaderBg()
  },
  beforeDestroy() {
    window.removeEventListener('resize', this.handleWindowResize)
    this.setHeaderBg()
  },
  computed: {
    lang() {
      return this.$store.state.lang.language
    }
  },
  methods: {
    handleWindowResize(event) {
      this.windowWidth = event.currentTarget.innerWidth
      this.setHeaderBg()
    },
    openApp() {
      window.open('https://app.trustdomains.org', '_blank')
    },
    setHeaderBg() {
      // y = x * 100/546 + 100 - 50000/546
      this.introPadding = this.windowWidth * 100 / 540 
      //console.log(this.introPadding)
    },
    toogleLanguage(lang) {
      if (lang == 'zh') {
        document.title = "Trust Domains - 可信去中心化金融生态"
      } else if (lang == 'en') {
        document.title = "Trust Domains - For Trust Decentralized Finance Economy"
      }
      this.$i18n.locale = lang
      this.$store.dispatch('ToogleLanguage', lang)
    }
  }
}
</script>

